'use strict';

module.exports = {
  addTimeout: function addTimeout(name, delay, callback, context) {
    this._timeouts = this._timeouts || {};
    if (this._timeouts.hasOwnProperty(name)) return;
    var self = this;
    this._timeouts[name] = global.setTimeout(function () {
      delete self._timeouts[name];
      callback.call(context);
    }, 1000 * delay);
  },
  removeTimeout: function removeTimeout(name) {
    this._timeouts = this._timeouts || {};
    var timeout = this._timeouts[name];
    if (!timeout) return;
    global.clearTimeout(timeout);
    delete this._timeouts[name];
  },
  removeAllTimeouts: function removeAllTimeouts() {
    this._timeouts = this._timeouts || {};

    for (var name in this._timeouts) {
      this.removeTimeout(name);
    }
  }
};